jQuery(document).ready(function($) {

    function getNonOrderableColumns(tableSelector) {
        let nonOrderable = [];
        $(`${tableSelector} thead th`).each(function(index) {
            if ($(this).attr("data-orderable") === "false") {
                nonOrderable.push(index);
            }
        });
        return nonOrderable;
    }

    $('#project-table').DataTable({
        destroy: true,
        responsive: true,
        language: {
            "decimal": ",",
            "thousands": ".",
            "sProcessing": "Traitement en cours...",
            "sSearch": "Rechercher :",
            "sLengthMenu": "_MENU_",
            "sInfo": "Affichage de _START_  _END_ sur _TOTAL_ lments",
            "sInfoEmpty": "Affichage de 0  0 sur 0 lments",
            "sInfoFiltered": "(filtr  partir de _MAX_ lments au total)",
            "sInfoPostFix": "",
            "sLoadingRecords": "Chargement en cours...",
            "sZeroRecords": "Aucun lment  afficher",
            "sEmptyTable": "Aucune donne disponible dans le tableau",
            "oAria": {
                "sSortAscending": ": activer pour trier la colonne par ordre croissant",
                "sSortDescending": ": activer pour trier la colonne par ordre dcroissant"
            }
        },
        columnDefs: [
            { orderable: false, targets: getNonOrderableColumns("#project-table") }
        ]

    });


    $('#files-table').DataTable({
        destroy: true,
        responsive: true,
        ordering: true,
        language: {
            "decimal": ",",
            "thousands": ".",
            "sProcessing": "Traitement en cours...",
            "sSearch": "Rechercher :",
            "sLengthMenu": "_MENU_",
            "sInfo": "Affichage de _START_  _END_ sur _TOTAL_ lments",
            "sInfoEmpty": "Affichage de 0  0 sur 0 lments",
            "sInfoFiltered": "(filtr  partir de _MAX_ lments au total)",
            "sInfoPostFix": "",
            "sLoadingRecords": "Chargement en cours...",
            "sZeroRecords": "Aucun lment  afficher",
            "sEmptyTable": "Aucune donne disponible dans le tableau",
            "oAria": {
                "sSortAscending": ": activer pour trier la colonne par ordre croissant",
                "sSortDescending": ": activer pour trier la colonne par ordre dcroissant"
            }
        },
        columnDefs: [
            { orderable: false, targets: getNonOrderableColumns("#files-table") }
        ]
    });
});
